// The BlueprintJS svg-icon inlining they use for the checkbox check icon
//  doesn't work for some reason, so we hardcode the svg ourselves.
.bp3-control.bp3-checkbox input:checked ~ .bp3-control-indicator:before {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M12 5c-.28 0-.53.11-.71.29L7 9.59l-2.29-2.3a1.003 1.003 0 00-1.42 1.42l3 3c.18.18.43.29.71.29s.53-.11.71-.29l5-5A1.003 1.003 0 0012 5z' fill='%23fff'/%3E%3C/svg%3E");
}

.bp3-control.bp3-checkbox input:indeterminate ~ .bp3-control-indicator:before {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M 11 7 H 5 c -0.55 0 -1 0.45 -1 1 s 0.45 1 1 1 h 6 c 0.55 0 1 -0.45 1 -1 s -0.45 -1 -1 -1 Z' fill='%23fff'/%3E%3C/svg%3E");
}

// Add the border to disabled state too
.bp3-control input:disabled ~ .bp3-control-indicator,
.bp3-control input:disabled:checked ~ .bp3-control-indicator {
  box-shadow: $eg-N4A 0px 0px 0px 1px inset;
}
